<template>
	<div class="auctions-section">
		<h2 class="auctions-title">{{ $localizeFilter('auctions').toUpperCase() }}</h2>
		<p class="auctions-subtitle">{{ $localizeFilter('auctions_subtitle') }}</p>
		<div class="auctions-wrapper">
			<div class="card" v-for="(auction, idx) in auctions" :key="idx" @click="openAuction(auction)">
				<div class="card-img">
					<template v-if="auction.type == 'coin'">
						<img :src="require('@/assets/newImages/coins/' + auction.coin + '.png')" alt="" />
					</template>
					<template v-else>
						<img :src="require('@/assets/newImages/coins/vectors/' + auction.currency1 + '.svg')" alt="" />
						<img :src="require('@/assets/newImages/coins/vectors/' + auction.currency2 + '.svg')" alt="" />
					</template>
					<div class="bd coinbd1"></div>
					<div class="bd coinbd2"></div>
					<div class="bd coinbd3"></div>
					<div class="bd coinbd4"></div>
				</div>
				<div class="card-bottom">
					<a target="_blank" class="title">{{ getTitle(auction) }}</a>
					<div class="text">{{ getText(auction) }}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import localizeFilter from '@/filters/localize.filter'
export default {
	name: 'Auctions',
	data() {
		return {

		}
	},
	computed: {
		auctions() {
			return this.$store.getters.auctions
		},
	},
	methods: {
		openAuction(auction) {
			if (auction.type == 'coin') {
				this.$router.push(`/cabinet/coinAuction?coin=${auction.coin}`)
			} else {
				this.$router.push(`/cabinet/providingAuction?poolId=${auction.poolId}&currency1=${auction.currency1}&currency2=${auction.currency2}&from=auctions`)
			}
		},
		getTitle(auction) {
			if (auction.type == 'coin') {
				return localizeFilter('coin') + ` ${auction.coin}`
			} else {
				return localizeFilter('pool') + ` ${auction.currency1} / ${auction.currency2}`
			}
		},
		getText(auction) {
			if (auction.type == 'coin') {
				return localizeFilter('coin_auction_subtitle') + ` ${auction.coin}`
			} else {
				return localizeFilter('pool_auction_subtitle') + ` ${auction.currency1} / ${auction.currency2}`
			}
		}
	},
	async mounted() {
		try {
			await this.$store.dispatch('getAuctions')
		} catch (e) {
			this.$error(e)
		}
	}
}
</script>

